body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  background: linear-gradient(180deg, rgba(125, 202, 255, 1) 0%, rgba(255, 255, 255, 1) 30%, rgb(255, 255, 194) 54%, rgb(62, 192, 101) 56%, rgba(112, 242, 35, 1) 100%);
}
/*ketu eshte bere ndryshimi i kodit*/
#bear {
  text-align: center;
  margin: 50px auto;
  width: 200px;
  height: 370px;
  position: relative;

  &::after {
    content: '';
    position: absolute;
    left: 50%;
    bottom: -10px;
    width: 400px;
    height: 60px;
    transform: translate(-50%, 0);
    border-radius: 50%;
    background: rgba(0, 0, 0, 0.1);
    filter: blur(5px);
  }

  &:hover{
    .hand-right {
      background: rgb(216, 137, 70);
      background: linear-gradient(135deg, rgba(216, 137, 70, 1) 14%, rgba(229, 173, 76, 1) 100%);
      border-radius: 50%;
      display: block;
      height: 60px;
      position: absolute;
      width: 130px;
      z-index: 1;
      right: -100px;
      transform-origin: 10px 50%;
      transform: rotate(20deg);
      animation: papa infinite 1s ease;
    
      @keyframes papa {
        from {
          transform: rotate(25deg);
        }
       
        to {
          transform: rotate(-12deg);
        }
      }
    }
  }

}
/*dhe ka perfundu ketu*/

.head-cnt {
  position: absolute;
  left: 50%;
  top: -10px;
  width: 230px;
  height: 180px;
  z-index: 2;
  transform: translate(-50%, 0);
  transform-origin: 50% 90%;
}

.head-bg {
  border-radius: 50%;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, rgba(232, 165, 84, 1) 40%, rgba(219, 145, 74, 1) 99%);
  position: relative;
  box-shadow: 0 3px 10px rgba(106, 69, 40, 0.1);
}

.ears {
  position: absolute;
  top: -10px;
  left: 50%;
  width: 100%;
  transform: translate(-50%, 0);
}

.ear {
  position: absolute;
  top: 0;
  border-radius: 50%;
  width: 80px;
  height: 80px;
  background-color: pink;
  background: linear-gradient(rgba(239, 199, 103, 1) 0%, rgba(202, 124, 58, 1) 100%);

  &:before {
    content: '';
    bottom: 16px;
    height: 50px;
    position: absolute;
    right: 13px;
    width: 50px;
    border-radius: 50%;
    background: linear-gradient(rgba(151, 93, 45, 1) 0%, rgba(111, 67, 38, 1) 100%);
  }
}

.ear-left {
  left: 0;
}

.ear-right {
  right: 0;
}

.eyes {
  width: 120px;
  height: 70px;
  position: absolute;
  z-index: 2;
  left: 50%;
  transform: translateX(-50%);
  top: 36px;
}

.eye {
  border-radius: 50%;
  background: linear-gradient(135deg, rgba(226, 160, 76, 1) 0%, rgba(239, 175, 88, 1) 99%);
  width: 50px;
  height: 70px;
  display: inline-block;
  position: absolute;
  top: 0;
  z-index: 2;

  &:before {
    content: "";
    border-top: 5px solid #89542A;
    border-radius: 50% 50% 0 0;
    display: block;
    height: 40px;
    width: 49px;
    position: absolute;
    left: 0;
    top: 0;
  }
}

/*ketu eshte bere ndryshimi i kodit*/
.eye-left {
  left: 0;
  transform: rotate(-25deg);

  &:hover{
    &:before {
      content: "";
      border-top: 5px solid #89542A;
      border-radius: 50% 50% 0 0;
      display: block;
      height: 40px;
      width: 49px;
      position: absolute;
      left: 0;
      top: 0;
      animation: pulse 0.3s ;
      animation-fill-mode: paused;
  
       @keyframes pulse {
        from {
          border-top: 5px solid #89542A;
        }
       
        to {
          border-top: 55px solid #89542A;
        }
      }
    }}
}



.eye-right {
  right: 0;
  transform: rotate(25deg);
}

.eyeball {
  background: linear-gradient(45deg, rgba(153, 86, 44, 1) 10%, rgba(109, 70, 39, 1) 100%);
  border-radius: 50%;
  height: 20px;
  width: 20px;
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
  top: 43px;

  &:before {
    content: "";
    width: 8px;
    height: 8px;
    border-radius: 50%;
    display: inline-block;
    background-color: #FFF;
    position: relative;
    top: 3px;
  }
}

.muzzle {
  background-color: #E5CA9D;
  border-radius: 50%;
  display: block;
  height: 65px;
  width: 95px;
  left: 50%;
  transform: translate(-50%, 0);
  position: absolute;
  bottom: 10px;
  z-index: 3;
  box-shadow: 0 3px 10px rgba(106, 69, 40, 0.2);
}

.nose {
  background-color: #66442a;
  border-radius: 70% / 70% 70% 131% 131%;
  height: 40px;
  width: 58px;
  position: absolute;
  left: 50%;
  top: -5px;
  transform: translate(-50%, 0);
  box-shadow: 0 3px 10px rgba(106, 69, 40, 0.3);

  &:after {
    content: "";
    display: block;
    height: 20px;
    left: 50%;
    transform: translate(-50%, 0);
    position: absolute;
    top: 34px;
    width: 2px;
    background-color: #6A4528;
  }
}

.mouth {
  border-bottom: 3px solid #78421C;
  border-radius: 0 0 90px 90px;
  height: 20px;
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
  top: 28px;
  width: 60px;
}

.body {
  width: 220px;
  height: 230px;
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
  top: 115px;
  z-index: 1;
}

.belly {
  border-radius: 50% 50% 50% 50% / 60% 60% 40% 40%;
  width: 100%;
  height: 100%;
  background: radial-gradient(ellipse at center, rgba(211, 132, 66, 1) 48%, rgba(183, 91, 38, 1) 99%);
  position: absolute;
  top: 0;
  z-index: 1;

  &:after {
    content: '';
    background-color: #FDEDD3;
    border-radius: 70% / 90% 90% 50% 50%;
    height: 140px;
    position: relative;
    top: 44px;
    width: 140px;
    display: inline-block;
    z-index: 2;
  }
}

.hands {
  position: absolute;
  left: 50%;
  top: 20px;
  width: 160px;
  height: 20px;
  transform: translate(-50%, 0);
}

.hand-left {
  background: rgb(216, 137, 70);
  background: linear-gradient(135deg, rgba(216, 137, 70, 1) 14%, rgba(229, 173, 76, 1) 100%);
  border-radius: 50%;
  display: block;
  height: 60px;
  position: absolute;
  width: 130px;
  z-index: 1;
  transform-origin: calc(100% - 10px) 50%;
  left: -100px;
  transform: rotate(-20deg);
}

.hand-right {
  background: rgb(216, 137, 70);
  background: linear-gradient(135deg, rgba(216, 137, 70, 1) 14%, rgba(229, 173, 76, 1) 100%);
  border-radius: 50%;
  display: block;
  height: 60px;
  position: absolute;
  width: 130px;
  z-index: 1;
  right: -100px;
  transform-origin: 10px 50%;
  transform: rotate(20deg);
}

.legs {
  position: absolute;
  bottom: 10px;
  width: 100%;
  z-index: 10;
  height: 80px;
}

.leg-left {
  border-radius: 50%;
  width: 96px;
  height: 120px;
  background: linear-gradient(135deg, rgba(223, 154, 76, 1) 41%, rgba(213, 132, 67, 1) 90%);
  position: absolute;
  transform: rotate(-45deg);
  left: -10px;

  &:before {
    content: '';
    display: block;
    background: linear-gradient(135deg, rgba(226, 200, 151, 1) 14%, rgba(253, 237, 211, 1) 100%);
    border-radius: 50%;
    width: 80%;
    height: 80%;
    position: relative;
    top: 10%;
    left: 10%;
  }
}

.leg-right {
  border-radius: 50%;
  width: 96px;
  height: 120px;
  background: linear-gradient(135deg, rgba(223, 154, 76, 1) 41%, rgba(213, 132, 67, 1) 90%);
  position: absolute;
  transform: rotate(45deg);
  right: -10px;

  &:before {
    content: '';
    display: block;
    background: linear-gradient(135deg, rgba(226, 200, 151, 1) 14%, rgba(253, 237, 211, 1) 100%);
    border-radius: 50%;
    width: 80%;
    height: 80%;
    position: relative;
    top: 10%;
    left: 10%;
  }
}


